.navbar{
    display: flex;
    justify-content: space-between;
    border: 1px solid black;
    align-items: center;
    padding-left: 20px;
    padding-right: 20px;
    background-color: rgb(255,255,255);
    height: 100px;
}
.navbar-button{
    width: 30px;
    height: 15px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.line-navbar{
    width: 100%;
    height:1px;
    background-color: black;
}